


export default class JobSelect extends FN.Widget {

	initAttrs(attrs = {}) {

		attrs = super.initAttrs(attrs) || {};

		return {
			trigger: null,
			initialValue: null,
			activeClassName: 'active',
			selectedClassName: 'active',
			roleTriggerText: 'span',
			roleMask: '[dropdown-role="mask"]',
			rolePanel: '[dropdown-role="panel"]',
			attrValue: 'dropdown-value',
			attrText: 'dropdown-text',
			...attrs
		};
	}

	_setValue(value) {
		if (value == null) return;

		const $option = this.$(this.get('rolePanel')).children(`[${this.get('attrValue')}="${value}"]`);
		if ($option.length <= 0) return;

		const selectedClassName = this.get('selectedClassName');
		$option.addClass(selectedClassName).siblings().removeClass(selectedClassName);

		if (this.get('roleTriggerText')) {
			$(this.get('trigger')).find(this.get('roleTriggerText')).text($option.attr(this.get('attrText')));
		}
	}

	render() {
		this._setValue(this.get('initialValue'));
	}

	initEvents() {
		$(this.get('trigger')).on('click', () => {

			if (!!this._isShowing) {
				this.hide();
			} else {
				this.show();
			}
		});

		this.$(this.get('rolePanel')).children().on('click', (event) => {
			this.handleSelect($(event.currentTarget));
		});
	}

	handleSelect($el) {
		const value = $el.attr(this.get('attrValue'));
		this._setValue(value);
		this.hide();
		this.trigger('select', value);
	}

	show() {
		this._isShowing = true;
		$('body').addClass('ovh');
		this.el.addClass(this.get('activeClassName'));

		// const ns = `click.${this.get('id')}`;

		// setTimeout(() => {
		// 	$(document).off(ns).on(ns, () => {
		// 		this.hide();
		// 		$(document).off(ns);
		// 	});
		// }, 30);
	}

	hide() {
		this._isShowing = false;
		$('body').removeClass('ovh');
		this.el.removeClass(this.get('activeClassName'));
	}

}


